.form {
  padding: 50px;
  padding-bottom: 55px;
  padding-top: 45px;
  display: grid;
  background-color: $color-tangora;
  color: $color-alice-blue;
  width: 303px;
  height: 355px;
  grid-gap: 18px;

  @media (max-width: $vp-767) {
    padding: 35px 30px 45px;
    width: 230px;
    height: 380px;
  }
}

.form__input {
  padding: 13px 16px;
  height: 17px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: $color-text-gray;
  border: none;
  border-radius: 0;
  outline:none;
}

.form__input--error {
  border: 1px solid $color-torch-red;
}

.form__input--good {
  border: 1px solid $color-good-green;
}


.form__header {
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  margin-bottom: 8px;

  .form__header-space {
    display: none;
  }

  @media (max-width: $vp-767) {
    line-height: 25px;
    padding: 0 7px;

    .form__header-space {
      display: block;
    }
  }
}

.form__checkbox--container {
  position: relative;
  display: flex;
}

.form__checkbox {
  position: relative;
  width: 18px;
  height: 18px;
  margin-bottom: 10px;

  &::before {
    content:"";
    width: 18px;
    height: 18px;
    position: absolute;
    background-color: $color-alice-blue;
  }

  &:checked {
    &::before {
      background-image: url('../img/svg/checkbox.svg');
      background-repeat: no-repeat;
      background-position-x: center;
      background-position-y: center;
    }
  }

  @media (max-width: $vp-767) {
    &::before {
      width: 20px;
      height: 20px;
    }
  }
}

.form__checkbox + span{
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  margin-left: 5px;
  margin-top: 3px;

  @media (max-width: $vp-767) {
    margin-top: 0;
    text-align: left;
    margin-right: 45px;
    margin-left: 17px;
  }
}

.form__btn {
  border: none;
  height: 43px;
  padding: 0 89px 0 90px;

  @media (max-width: $vp-767) {
    padding: 0 53px;
    margin-top: 10px;
  }
}

.form__checkbox--container-good {
  box-shadow: 0 0 8px $color-torch-red;
}
